import { h, render, Component } from 'preact';
import { Link } from 'react-router-dom';

import Category from '../../sub/category';
import Banners from '../../sub/banners';
import RecentActivity from '../../sub/recentActivity';
import Spotlight from '../../sub/spotlights';
import Collections from '../../sub/collection';
import Alert from '../../sub/alert';
import PopularAds from '../../sub/popularAds';
import RecommendedAds from '../../sub/recommendedAds';
import DownloadApp from '../../sub/downloadApp';
import BottomAd from '../../sub/qdfpAds/bottomAd';


export default class Homepage extends Component{

	componentDidMount() {
		if (typeof ga !=='undefined')
			this.setPageView();
		else {
			this.gaTimer = setInterval( this.setPageView, 3000);
		}
	}

	setPageView = () => {
		if (typeof ga !=='undefined'){
			ga('send','pageview','/quikr_HP_dual&lang=English');
			clearInterval(this.gaTimer);
		}
	}

	render(props){
		return (
			<div>
				<Category />
				<Banners />
				<RecentActivity />
				{/* <Spotlight /> */}
				{/* <Collections /> */}
				{/* <Alert /> */}
				<PopularAds />
				{/* <RecommendedAds /> */}
				<BottomAd />
				<DownloadApp />
				<Link to="/xyz/123" >XYZ</Link>
			</div>
		);
	}
}



