import { h, render, Component } from 'preact';

import Card from 'q-mdc/elements/material-card';
import CardAction from 'q-mdc/elements/material-card-actions';
import CardPrimary from 'q-mdc/elements/material-card-primary';
import CardTitle from 'q-mdc/elements/material-card-title';
import CardSubTitle from 'q-mdc/elements/material-card-subtitle';
import CardSupportingText from 'q-mdc/elements/material-card-supporting-text';
import CardMedia from 'q-mdc/elements/material-card-media';
import CardMediaItem from 'q-mdc/elements/material-card-media-item';
import CardHorizontalBlock from 'q-mdc/elements/material-card-horizontal-block';

import styles from './index.scss';

export default class Activity extends Component{
	render(props){
		return (
			<Card class="component-wrap recent-activity">
				<CardTitle>
					{props.title}
					{props.links}
				</CardTitle>
				<CardHorizontalBlock>
					<CardMediaItem src={props.media} />
					<CardSupportingText>
						{props.description}
						{props.actions && props.actions.map(action => (<CardAction>{action}</CardAction>))}
					</CardSupportingText>
				</CardHorizontalBlock>
			</Card>
		);
	}
}


