import { h, render, Component } from 'preact';
import styles from './index.scss';
import Button from 'q-mdc/elements/material-button';
import AlertIcon from '../../../icons/alert';

export default class Alert extends Component {
	render(props, state, context) {
		return (
			<div class="component-wrap alert-box">
				<figure>
					<div class="inner-circle">
						<AlertIcon />
					</div>
				</figure>
				<p>Create <strong>free alert</strong> and get updates from <strong>Quikr?</strong></p>
				<Button
					ripple={true}
	                compact={true}
	                dense={true}
	                accent={true}
	                stroked={true}
	                >Create Alert</Button>
			</div>
		);
	}
}


