import { h, render, Component } from 'preact';
import styles from './index.scss';
import Button from 'q-mdc/elements/material-button';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { isAndroid } from '../../../../helpers/utils/userAgent';
import { getEventTrackGA } from '../../../../helpers/utils/ga';

function redirectToPlayStore () {
	if (typeof window !== 'undefined'){
		window.location = 'https://play.google.com/store/apps/details?id=com.quikr&referrer=utm_source%3Dwebsite%26utm_medium%3Dwebsite' ;
	}
}

function redirectToIosStore () {
	if (typeof window !== 'undefined'){
		window.location = 'https://itunes.apple.com/in/app/quikr-free-local-classifieds/id632051273?mt=8';
	}
}
class DownloadApp extends Component {

	pushGaAndRedirect = (e) => {
		e.preventDefault();
		e.stopPropagation();
		if (e.nativeEvent) {
			e.nativeEvent.stopImmediatePropagation();
		}
		const { isAndroid } = this.props;
		const gaEventObject = isAndroid ? { category: 'quikr',action: 'quikr_homepage_click',label: 'quikr_downloadapp_Android', hitCallback: redirectToPlayStore } : { category: 'quikr',action: 'quikr_homepage_click',label: 'quikr_downloadapp_iOS', hitCallback: redirectToIosStore };
		getEventTrackGA(gaEventObject);
	}
	
	
	render(props, state, context) {
		const { isAndroid } = props;
		return isAndroid ? (
			<div class="experience-app-box" onClick={this.pushGaAndRedirect}>
				<h3>Experience Quikr App</h3>
				<p>Donwnload from play store</p>
				<Button class="app-stores google-play-btn">Google Play</Button>
				<div class="app-screen android" />
			
			</div>
		):(<div class="experience-app-box" onClick={this.pushGaAndRedirect}>
			<h3>Experience Quikr App</h3>
			<p>Donwnload from App store</p>
			<Button class="app-stores apple-store-btn">Apple Store</Button>
			<div class="app-screen apple" />
		</div>);
	}
}

const mapStateToProps = state => ({
	isAndroid: isAndroid( get(state,'globalContainer.userAgent',''))
});

const connectedDownloadApp = connect(mapStateToProps, null)(DownloadApp);

export default connectedDownloadApp;


