import { h, render, Component } from 'preact';
import List from 'q-mdc/elements/material-list';
import ListItem from 'q-mdc/elements/material-list-item';
import { connect } from 'react-redux';
import get from 'lodash/get';
import shuffle from 'lodash/shuffle';

import { getPWALinks } from '../../../../redux/actions/links';
import { getCityIdCookie, getCityNameCookie } from '../../../../helpers/utils/cookies';

import { Bike, Car, Sports, Salon, KidsToys, CertifiedMob, Mobile, Electronics, Lifestyle, Homes, Services, Jobs, Education, Entertainment, Furniture, Pets, Community, Events, Matrimonial } from 'q-components/components/icons/categoryIcons';
import { More, Less } from '../../../icons/moreLessIcons';

import styles from './index.scss';

const topFiveCats = [
	{ name: 'Services',  icon: <Services />, key: 'services', href: 'https://www.quikr.com/services/gId-123' },
	{ name: 'Homes', icon: <Homes />, key: 'real-estate', href: 'https://www.quikr.com/real-estate/gId-20' },
	{ name: 'Jobs',  icon: <Jobs />, key: 'jobs', href: 'https://www.quikr.com/jobs' },
	{ name: 'Cars',   icon: <Car />, key: 'cars-bikes', href: 'https://www.quikr.com/cars-bikes/gId-60' },
	{ name: 'Bikes',  icon: <Bike />, key: 'bikes', href: 'https://www.quikr.com/bikes/gId-72' }
];

const categories = [
	
	
	{ name: 'Education',  icon: <Education />, key: 'education-training', href: 'https://www.quikr.com/education-training' },
	{ name: 'Mobiles',  icon: <Mobile />, key: 'mobiles-tablets', href: 'https://www.quikr.com/Escrow/mobiles-tablets/gId-269' },
	{ name: 'Furniture', icon: <Furniture />, key: 'furniture-decor', href: 'https://www.quikr.com/Escrow/furniture-decor/gId-40?filter=fd' },

	{ name: 'Electronics',  icon: <Electronics />, key: 'electronics-appliances', href: 'https://www.quikr.com/Escrow/electronics-appliances/gId-247' },
	{ name: 'Kids & Toys',  icon: <KidsToys />, key: 'kids-toys', href: 'http://www.quikr.com/Escrow/kids-toys/gId-40?filter=kt' },
	{ name: 'Sports',  icon: <Sports />, key: 'sports-hobbies-fashion', href: 'https://www.quikr.com/Escrow/sports-hobbies-fashion/gId-40?filter=sbh' }
];

const salonAtHome = { name: 'Salon at Home',  icon: <Salon />, key: 'salon-at-home', href: 'http://www.athomediva.com' };

const moreCategories = [
	
	{ name: 'Pets',  icon: <Pets />, key: 'pets-pet-care', href: 'http://www.quikr.com/pets-pet-care/gId-246' },
	{ name: 'Certified Mobiles',  icon: <CertifiedMob />, key: 'refurbished-phones', href: '//www.quikr.com/QuikrX/Refurbished-Mobile-Phones/refurbished/w1388?l=condition' },
	{ name: 'Lifestyle',  icon: <Lifestyle />, key: 'home-lifestyle', href: 'https://www.quikr.com/Escrow/home-lifestyle/gId-40?filter=hl' },
	{ name: 'Entertainment',  icon: <Entertainment />, key: 'entertainment', href: 'http://www.quikr.com/entertainment/gId-179' },

	{ name: 'Community',  icon: <Community />, key: 'community',href: 'https://www.quikr.com/community/gId-1' },
	{ name: 'Events',  icon: <Events />, key: 'events', href: 'https://www.quikr.com/events/gId-281' },
	{ name: 'Matrimonial',  icon: <Matrimonial />, key: 'matrimonial', href: 'https://www.quikr.com/matrimonial/gId-161' }];

class Category extends Component {

	state = { showMore: false, shuffledCats: topFiveCats };

	componentDidMount() {
		this.setState({
			shuffledCats: shuffle(topFiveCats)
		});
		this.getCategories();
	}

	getCategories = () => {
		const { globalContainer } = this.props;
		const cityId = getCityIdCookie() || 1;
		const cityName = getCityNameCookie() || 'www';
		const login = get(globalContainer, 'visitor.loggedin', false);
		const userId = get(globalContainer, 'visitor.user.userId', false);
		this.props.getPWALinks({ cityId, cityName, userId, login });
	}

	showMoreCategories = (e) => {
		e.preventDefault();
		this.setState({
			showMore: true
		});
		
	}

	hideMoreCategories = () => {
		this.setState({
			showMore: false
		});
	}

	render(props, state, context) {
		const { showMore, shuffledCats } = state;
		const { links } = props;
		const categoryLinks = get(links, 'metaCats');
		return (
			<div class="component-wrap" >
				<List links class="category-box">
					<div class="top-categories">
						{shuffledCats.map((category) => (<ListItem link href={get(categoryLinks, `${category.key}.href`, category.href)} key={category.key} >
	              		<i class="category-icons" aria-hidden="true">
								{category.icon}
							</i>
							<label>{category.name}</label>
						</ListItem>
						))}
				 {categories.map((category) => (<ListItem link href={get(categoryLinks, `${category.key}.href`, category.href)} key={category.key} >
          			<i class="category-icons" aria-hidden="true">
								{category.icon}
							</i>
							<label>{category.name}</label>
						</ListItem>
						))}
				 {showMore ?  (<ListItem link href={get(categoryLinks, `${salonAtHome.key}.href`, salonAtHome.href)} key={salonAtHome.key} >
	              		<i class="category-icons" aria-hidden="true">
								{salonAtHome.icon}
							</i>
							<label>{salonAtHome.name}</label>
						</ListItem>) : (<ListItem link onClick={this.showMoreCategories} key="more">
							<i class="category-icons" aria-hidden="true">
								<More />
							</i>
							<label>More</label>
						</ListItem>)}
					</div>
					<div class={`showHide ${showMore ? 'show' : null}`}>{moreCategories.map((category) => (<ListItem link href={get(categoryLinks, `${category.key}.href`, category.href)} key={category.key} >
	              		<i class="category-icons" aria-hidden="true">
							{category.icon}
						</i>
						<label>{category.name}</label>
					</ListItem>
					))}
					{showMore ? (<ListItem link onClick={this.hideMoreCategories} key="less">
						<i class="category-icons" aria-hidden="true">
							<Less />
						</i>
						<label>Less</label>
					</ListItem>) : null}</div>
					
				</List>
			</div>
		);
	}
}

const mapStateToProps = state => ({
	links: state.links,
	globalContainer: state.globalContainer
});

const mapDispatchToProps = dispatch => ({
	getPWALinks: ({ cityName, cityId, userId, login }) => dispatch(getPWALinks({ cityName,cityId,login,userId }))
});

const connectedCategory = connect(mapStateToProps, mapDispatchToProps)(Category);

export default connectedCategory;


