import { h, render, Component } from 'preact';
import Button from 'q-mdc/elements/material-button';
import Ads from '../../../presentation/ads';
import { getPopularAds } from '../../../../redux/actions/ads';
import { connect } from 'react-redux';
import get from 'lodash/get';

const ads = [
	{
		media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
		title: 'ford fiesta 1.4 duratorq diesel sxi',
		loc: 'Whitefield',
		price: '4,50,000'
	},
	{
		media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
		title: 'Hyundai i10 diesel sxi',
		loc: 'Kamnnahalli',
		price: '2,50,000'
	},
	{
		media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
		title: 'Maruti Suzuki diesel LXI',
		loc: 'M.G. Road',
		price: '8,50,000'
	},
	{
		media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
		title: 'Hyundai i10 diesel sxi',
		loc: 'Kamnnahalli',
		price: '2,50,000'
	},
	{
		media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
		title: 'Maruti Suzuki diesel LXI',
		loc: 'M.G. Road',
		price: '8,50,000'
	}
];

class PopularAds extends Component {

	componentDidMount() {
		this.props.getPopularAds();
	}

	render(props, state, context) {
		const { popularAds: { data, AdCount } } = props;
		return AdCount > 0 ? (
			<div class="row-base">
				<h3 class="section-title">Popular Ads
				{/* <a href="#">MORE</a> */}
				</h3>
				<div class="component-wrap">
					<div class="scroll__x-axis">
						{data.map(({ title, adUrl, imgUrl, price, ...rest }, index) => (
							<Ads title={title} media={imgUrl} price={price} href={adUrl} {...rest} />
						))}
				    </div>
				</div>
			</div>
		) : null;
	}
}


const mapStateToProps = state => ({
	popularAds: state.ads.popularAds
});

const mapDispatchToProps = dispatch => ({
	getPopularAds: () => dispatch(getPopularAds())
});

const connectedPopularAds = connect(mapStateToProps, mapDispatchToProps)(PopularAds);

export default connectedPopularAds;


