import { h, render, Component } from 'preact';
import Button from 'q-mdc/elements/material-button';
import { connect } from 'react-redux';
import get from 'lodash/get';

import { getRecentActivities } from '../../../../redux/actions/globalContainer';

import Forward from '../../../icons/forward';
import Activity from '../../../presentation/activity';
import Thumbnail from '../../../../../assets/images/thumbnail.jpg';

const recentActivity=
	[
		{
			title: 'Payment Pending',
			links: <a href="#"> More Offer <Forward /></a>,
			media: Thumbnail,
			description: <p>Seller has accepted your offer of <strong>5000</strong> for the ad <strong>Nokia 1100 Sale with  excellent condition.</strong></p>,
			action: <Button primary ripple dense unelevated>Pay Now</Button>
		},
		{
			title: 'Incomplete Post Ad',
			links: <a href="#"> More Offer <Forward /></a>,
			media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
			description: <p>Seller has accepted your offer of <strong>5000</strong> for the ad <strong>Nokia 1100 Sale with  excellent condition.</strong></p>,
			action: <Button primary ripple dense unelevated>Pay Now</Button>
		},
		{
			title: 'Incomplete Ad',
			links: <a href="#"> More Offer <Forward /></a>,
			media: 'https://teja8.kuikr.com/images/carNoImage.jpg',
			description: <p>Seller has accepted your offer of <strong>5000</strong> for the ad <strong>Nokia 1100 Sale with  excellent condition.</strong></p>,
			action: <Button primary ripple dense unelevated>Pay Now</Button>
		}
	];

class RecentActivity extends Component {

	componentDidMount(){
		this.props.getRecentActivities();
	}

	render(props, state, context) {

		const { activities: { activities } } = props;
		return (
			<div class="scroll__x-axis">
				{/*recentActivity.map((activity, index) =>
					 (
						<Activity key={index} title={activity.title} description={activity.description}  />
					)
				)*/}
				{activities && activities.map((activity, index) => activity ? (
					<Activity key={index} title={activity.main.title} description={activity.main.description} actions={activity.desc}  />
				): null)}
			</div>
		);
	}
}

const mapStateToProps = state => ({
	activities: get( state ,'globalContainer.activities', { activities: [] })
});

const mapDispatchToProps = dispatch => ({
	getRecentActivities: () => dispatch(getRecentActivities())
});

const connectedRecentActivity = connect(mapStateToProps,mapDispatchToProps)(RecentActivity);

export default connectedRecentActivity;



