import { h, render, Component } from 'preact';

import List from 'q-mdc/elements/material-list';
import ListItem from 'q-mdc/elements/material-list-item';
import style from './index.scss';
import { connect } from 'react-redux';
import get from 'lodash/get';
import { getHomepageBanners } from '../../../../redux/actions/banners';

class Banners extends Component{

	componentDidMount() {
		this.props.getBanners();
	}

	render(){

		const { banners } = this.props;
		return banners.length > 0 ? (
			<div class="component-wrap banner-cont">
				<div class="scroll__x-axis">
					<List links>
						{banners.map((banner, index) => (<ListItem link href={banner.href} title={banner.title} key={index}><img src={banner.image} /></ListItem>))}
		            </List>
				</div>
			</div>
		) : null;
	}
}

const mapStateToProps = state => ({
	banners: get(state,'banners',[])
});

const mapDispatchToProps = dispatch => ({
	getBanners: () => dispatch(getHomepageBanners())
});

const connectedBanners = connect(mapStateToProps, mapDispatchToProps)(Banners);

export default connectedBanners;



