import {  h, render, Component } from 'preact';
import { connect } from 'react-redux';
import get from 'lodash/get';
import styles from './index.scss';

class BottomAd extends Component  {
	
	componentWillReceiveProps(newProps) {
		if (this.props.JsContext !== newProps.JsContext) {
			const { JsContext } = newProps;
			if (typeof window !=='undefined') {
				window.qdfp_vp = { id: JsContext.pageId, ptargeting: [{ key: 'City',value: JsContext.currentCity },{ key: 'Category',value: 'undefined' },{ key: 'SubCat',value: 'undefined' },{ key: 'Locality',value: '' }] } ;
				const script = document.createElement('script');
				script.src = 'https://teja9.kuikr.com/public/mon/qapqdfp/4.0.0/horizontal/index.js';
				script.async = true;
        
				script.onload = function() {
					console.log('script loaded');
				};
        
				document.body.appendChild(script);
			}
		}
	}
    
	componentDidMount() {
		const { JsContext ={} } = this.props;
		if (typeof window !=='undefined') {
			window.qdfp_vp = { id: JsContext.pageId, ptargeting: [{ key: 'City',value: JsContext.currentCity },{ key: 'Category',value: 'undefined' },{ key: 'SubCat',value: 'undefined' },{ key: 'Locality',value: '' }] } ;
			const script = document.createElement('script');
			script.src = 'https://teja9.kuikr.com/public/mon/qapqdfp/4.0.0/horizontal/index.js';
			script.async = true;
    
			script.onload = function() {
				console.log('script loaded');
			};
    
			document.body.appendChild(script);
		}
	}
	render() {
		return <div class="component-wrap btm-ad-box" id="dfp_desktop_hp" />;
	}
}

const mapStateProps = state => ({
	JsContext: get(state,'globalContainer.JsContext', {})
});


const connectedBottomAd = connect(mapStateProps, null)(BottomAd);

export default connectedBottomAd;


