import { h, render, Component } from 'preact';
import Tabs from 'q-mdc/elements/material-tabs';
import style from './index.scss';

export default class Tab extends Component{
	constructor(props){
		super(props);
		this.state = { active: 0, content: props.tabs[0].content };
	}
	changeTab = (active) => {
		this.setState({ active });
	};
	render(props, state ,context){
		const { tabs } = props;
		const { active } = state;

		return (
			<div class="component-wrap spotlights" >
				<div>
					<Tabs  id="test">
						{tabs.map((tab, index) => (<Tabs.Tab active={active===index} key={index} onClick={(e) => this.changeTab(index)} >{tab.title}</Tabs.Tab>))}
					</Tabs>
			
				
				</div>
				{tabs[active].content()}
			</div>
		);
	}
}


