export const getPopularAds = () => ({
	type: 'GET_POPULAR_ADS',
	axios: true,
	options: {
		url: `/core/quikrcom/ad/fetch-popular-ads`,
		method: 'post',
		data: `reqfrom=REQUEST_INDEXPAGE&gSubCatId=0`
	}
});


