export const getPWALinks = ({ cityName,cityId,userId,login }) => {
	const requestObject = new URLSearchParams(`cityName=${cityName}&cityId=${cityId}&userId=${userId}&login=${login}`);
	return {
		type: 'GET_PWA_LINKS',
		axios: true,
		options: {
			url: 'https://feservices.quikr.com/v2/header/get_links_pwa',
			method: 'post',
			data: requestObject,
			headers: {
				accept: 'application/json, text/javascript, */*; q=0.01',
				'accept-language': 'en-GB,en-US;q=0.9,en;q=0.8',
				'Content-Type': 'application/x-www-form-urlencoded'
			},
			json: true
		}

	};
};


